namespace Updraft {
	
	
	export class DbSequence {
		private statements: {sql: string, args: any[], callback: DbResultsCallback}[];
		constructor() {
			this.statements = [];
		}
		executeSql(sql: string, args?: any[], callback?: DbResultsCallback) {
			verify(!callback, "no callbacks yet");
			this.statements.push({sql, args, callback});
		}
		toCallback(): DbTransactionCallback {
			let statements = this.statements;
			let idx = 0;
			let cb = (transaction: DbTransaction): Promise<any> => {
				if (idx < statements.length) {
					let next = this.statements[idx];
					idx++;
					transaction.executeSql(next.sql, next.args, cb);
				}
				return null;
			};
			return cb;
		}
	}
	
	export interface DbWrapper {
		transaction(callback: DbTransactionCallback): Promise<any>;
		readTransaction(callback: DbTransactionCallback): Promise<any>;
		sequence(seq: DbSequence): Promise<any>;
	}
	
	export interface DbTransactionCallback {
		(transaction: DbTransaction): Promise<any>;
	}
	
	export interface DbTransaction {
		executeSql(sql: string, params?: (string | number)[], callback?: DbResultsCallback): Promise<any>;
		each(sql: string, params: (string | number)[], callback: DbEachResultCallback): Promise<any>;
	}
	
	export interface DbResultsCallback {
		(transaction: DbTransaction, results: any[]): any | Promise<any>;
	}
	
	export interface DbEachResultCallback {
		(transaction: DbTransaction, result: any): any | Promise<any>;
	}
}
